* {
    margin: 0;
    padding: 0;
}

.search-form {
    border: 3px solid black;
    width: 100%;
    height: 90px;
    display: flex;
    /* padding: 0, 300px, 0, 300px; */
    /* justify-content: center; */
    align-items: center;
    background: -webkit-linear-gradient(left, rgba(175,212,150,1) 0%,rgba(160,198,122,1) 71%,rgba(160,198,122,1) 71%,rgba(160,198,122,1) 84%);
}

.search-form input {
    height: 50px;
    border: 3px solid red;
    /* border-image: linear-gradient(45deg, purple, orange) 1; */
    border-radius: 40px;
    width: 250px;
    padding: 0px 30px;
    margin: 0px 50px 0px 100px;
}

